ul li {
  /* padding: 0; */
  text-align: left;
}
.section-wrap {
  padding-left: 80px;
}
.section-desc {
  text-align: left;
  margin-left: 50px;
}
.section-desc h1 {
  margin: 0;
  margin-bottom: 24px;
}
.section-desc p {
  font-style: italic;
  font-weight: 200;
  line-height: 60px;
  font-size: 30px;
  margin: 0;
}
.subcribe-wrap {
  display: flex;
  margin: 50px 0;
  justify-content: end;
}
.subcribe-wrap .textinput {
  width: 400px;
  margin-right: 24px;
}
.section-module {
  display: flex;
  flex-wrap: wrap;
  padding: 24px;
}
.section-show {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-image: url("../../assets/imgs/wave.svg");
  background-position: left bottom;
}
.row {
  display: flex;
}
.section-title {
  position: relative;
  font-size: 40px;
  margin: 40px 0;
  letter-spacing: 0.5ch;
}
.section-title::after {
  position: absolute;
  left: 47%;
  top: 60px;
  bottom: 0;
  content: "";
  width: 80px;
  height: 3px;
  background-color: rgb(12, 94, 225);
}
.show-wrap {
  display: flex;
}
.blog-wrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 240px;
}
.blog-item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.blog-item .bgimg {
  width: 500px;
}
.blog-item .left {
  width: 400px;
  text-align: left;
  margin-right: 24px;
}
.blog-item .left h1 {
  margin-bottom: 24px;
}

.blog-item .left span {
  color: #77676781;
}
.contact-warp {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}
.contact-warp h1 {
  margin-bottom: 120px;
}
.contact-warp h1::after {
  left: 60px;
}
.method a {
  margin-right: 60px;
}
.method :nth-last-child(1) {
  margin: 0;
}
.method {
  display: flex;
  align-items: center;
}
.header-control {
  display: flex;
  align-items: center;
}
.search {
  display: flex;
  border: 2px solid #f9f9f9;
  border-radius: 5px;
  margin-right: 24px;
}
