.home-wrap {
  width: 100%;
  height: 100%;
}
.header-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
  padding: 0 24px;
  box-shadow: 0 -4px 4px 4px #000;
}
.header-wrap a {
  display: inline-block;
  height: 66px;
  text-decoration: none;
  text-align: center;
  line-height: 66px;
  margin-right: 24px;
  color: #8590a6;
}

.nav-active {
  border-bottom: 4px solid #06f;
  font-weight: 600;
  color: #121212 !important;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
